@import "./components/utils.css";

::-moz-selection {
  color: var(--black);
  background: var(--yella);
}

::selection {
  color: var(--black);
  background: var(--yella);
}

body {
  margin: 0;
  font-family: var(--serif);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--black);
}

code {
  font-family: var(--code);
}

h1,
h2 {
  font-family: var(--sans-serif);
}
