@import "./utils.css";

:root {
  --header-marginTop: 6em;
}

.header-container {
  margin-top: var(--header-marginTop);
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.header-title {
  margin: 0 0 0.4em 0;
  font-size: 3em;
  color: var(--red);
}

.header-social {
  margin: 0 0 1.4em 0;
  display: flex;
  width: 240px;
  justify-content: space-between;
  align-items: center;
}

.header-icon {
  max-height: 20px;
  max-width: 30px;
}

.header-social:hover .header-icon {
  opacity: 0.6;
}

.header-social .header-icon:hover {
  opacity: 1;
}

.header-blurb {
  max-width: 400px;
  font-size: 1em;
}
