:root {
  --thumb-maxWidth: 500px;
}

.thumb-container {
  position: relative;
  max-width: 100%;
  margin-bottom: 1em;
}

.thumb-hoverContainer {
  --topPadding: 20px;
  --leftPadding: 35px;

  position: absolute;
  top: 0;
  left: 0;
  padding: var(--topPadding) var(--leftPadding);
  width: calc(100% - 2 * var(--leftPadding));
  height: calc(100% - 2 * var(--topPadding));
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.7);

  opacity: 0;
  transition: opacity 0.2s;
}

.thumb-container:hover .thumb-hoverContainer {
  opacity: 1;
}

.thumb-title {
  font-family: var(--sans-serif);
  font-weight: 600;
  font-size: 2em;
}

.thumb-art {
  max-width: 100%;
}
